import React from 'react';

import Layout from '../../components/Layout';

import magri_frog_casestudyimage from '../../assets/images/project_image_govi_casestudy_img.png';
import magri_frog_articleimage from '../../assets/images/project_image_govi_article_img.png';
import snacker_design_process_image from '../../assets/images/project_image_snacker_design_process.png';
import snacker_pain_point_image from '../../assets/images/project_image_snacker_pain_points.png';
import snacker_user_persona_image from '../../assets/images/project_image_snacker_user_persona.png';
import snacker_user_flow from '../../assets/images/project_image_snacker_user_flow.png';
import snacker_user_journey_map from '../../assets/images/project_image_snacker_user_journey_map.png';
import snacker_paper_wireframes from '../../assets/images/project_image_snacker_paper_wireframes.png';
import snacker_digital_wireframes from '../../assets/images/project_image_snacker_digital_wireframes.png';
import snacker_lo_fi_prototype from '../../assets/images/project_image_snacker_lo_fi_prototype.png';
import snacker_affinity_diagram from '../../assets/images/project_image_snacker_affinity_diagram.png';
import snacker_usertest_changes from '../../assets/images/project_image_snacker_changes_after_user_testing.png';
import snacker_ui_elements from '../../assets/images/project_image_snacker_ui_elements.png';
import snacker_mockup_gif from '../../assets/images/snacker_app_mockup_.gif';
import petadoption_design_process from '../../assets/images/project_image_petadoption_design_process.png';
import petadoption_emapthy_map_gif from '../../assets/images/petshelter_web_emapthy_maps.gif';
import petadoption_user_personas_gif from '../../assets/images/petadoption_user_personas.gif';
import petadoption_userstory_problemstatement from '../../assets/images/project_image_petadoption_user_story_problem_statement.png';
import petadoption_user_painpoints from '../../assets/images/project_image_petadoption_user_pain_points.png';
import petadoption_site_map from '../../assets/images/project_image_petadoption_site_map.png';
import petadoption_paper_wireframes from '../../assets/images/project_image_petadoption_paper_wireframes.png';
import petadoption_digital_wireframes from '../../assets/images/project_image_petadoption_digital_wireframes.png';
import petadoption_affinity_diagram from '../../assets/images/project_image_petadoption_affinity_diagram.png';
import petadoption_lofi_prototype from '../../assets/images/project_image_petadoption_lofi_prototype.png';
import petadoption_after_changes from '../../assets/images/project_image_petadoption_after_changes.png';
import petadoption_ab_testing from '../../assets/images/project_image_petadoption_ab_testing.png';
import petadoption_responsive_mockup from '../../assets/images/project_image_petadoption_responsive_mockup.png';
import petadoption_scaling_wireframes from '../../assets/images/project_image_petadoption_scaling_wireframes.png';
import petadoption_all_digital_responsive_screens from '../../assets/images/project_image_petadoption_all_digital_responsive_screens.png';
import teenagefinaceapp_design_process from '../../assets/images/project_image_teenagefinace_design_process.png';
import teenagefinaceapp_user_persona from '../../assets/images/project_image_teenagefinance_user_persona.png';
import teenagefinaceapp_user_story_problem_statement from '../../assets/images/project_image_teenagefinance_user_story_problem_statement.png';

const IndexPage = () => (
  <Layout fullMenu>
    <section className="wrapper-common" id="wrapper-pet-shelter-web">
      <header>
        <div className="inner">
          <h2>Finance Learning App and Website for Teenagers</h2>
          <p>Engaging hearts, equipping minds.</p>
        </div>
      </header>

      <div className="wrapper">
        <div className="inner">
          <h3 className="major">The App and Website</h3>
          <p>
            The final project for the UX certification program by Google was to
            design for social good. The topic received was to ‘design a way to
            help tutor teenagers in financial literacy’.According to Fox
            business news a study in 2020 by ING found out that “83% of teens
            don’t know how to manage money”. ‘Learn Finance’ aims to educate the
            next billion users about practical financial management and improve
            teenagers’ appetite for learning through its interactive UX learning
            platform.
          </p>
          <h3 className="major">My Role</h3>
          <p>
            Researcher,UX designer, UX writer, UX tester from concept to
            delivery
          </p>
          <h3 className="major">Project goal</h3>
          <p>
            To design a tool that helps teenagers learn and practice their
            financial management skills. For instance this tool will consist of
            free courses and bit sized crucial information on how to practically
            better manage your finances and later a platform to document your
            basic finances.
          </p>
          <h3 className="major">Design Process</h3>
          <img
            className="image main"
            src={teenagefinaceapp_design_process}
            alt=""
          />
          <h3 className="major">Target audience</h3>
          <p>
            Learn Finances' primary users include teenagers who are concerned
            with managing their finance in a more inclusive and fun way. These
            are individuals who currently have no or limited financial control
            practices to manage their finances and reside in urban , suburban
            and rural areas.
          </p>
          <h3 className="major">Key challenges</h3>
          <p>
            Empathising researching and user testing teenagers during post covid
            times.
          </p>
          <h3 className="major">Empathize</h3>
          <h3>Research study</h3>
          <p>
            We conducted user research and received feedback from users that we
            turned into user personas. Jacob is a 14 year old highschool student
            who depends on his parents for money. He is interested in learning
            how to manage his expenses so that he can buy himself the bike he
            has always been dreaming of. The research revealed that Jacob was
            frustrated by the technical jargon on finance management available
            online . We also learnt that Jakob needs a progress chart to know
            his status and keep him motivated to achieve his targets. Jacob
            would like to learn in a more engaging way and use the concepts he
            learns in his daily life.
          </p>
          <img
            className="image main"
            src={teenagefinaceapp_user_persona}
            alt=""
          />
          <h3>Problem</h3>
          <p>
            Currently there is no platform that offers finance lessons a
            teenager needs in a form they understand
          </p>
          <img
            className="image main"
            src={teenagefinaceapp_user_story_problem_statement}
            alt=""
          />
          <h3 className="major">Ideate</h3>
          <p>
            We identified some concepts for our app’s design based on what we
            learnt about our users in the empathize stage using the How might we
            approach. We rapidly brainstormed multiple creative solutions based
            on what we learnt from our research to identify various ways we
            could be designing.
          </p>
          <p>
            <ul>
              <li>
                Amp The good- how might we make learning finance fun among
                teenagers ?
              </li>
              <li>
                Explore the opposite- how might we create a way to hate finance
                learning?
              </li>
              <li>
                Change the status quo -how might we make learning feel like you
                are not learning ?
              </li>
              <li>
                Break the point of view into pieces -
                <ul>
                  <li>
                    How might we make online learning more interesting for a
                    teenager?
                  </li>
                  <li>
                    How might we make online teaching more interesting for a
                    teacher?
                  </li>
                  <li>
                    How might we practice learning in everyday teenage life?
                  </li>
                </ul>
              </li>
              <li>
                Remove the Bad- how might we make a teenager learn finance
                without teaching them?
              </li>
              <li>
                Go after adjectives- how to make learning less stressful for
                teenagers?
              </li>
              <li>
                Question an assumption- How might we remove the weight of the
                topic when learning finance for teenagers?
              </li>
              <li>
                Create an analogy using established needs or context- how might
                we make learning finance feel like playing a video game?
              </li>
              <li>
                Identify unexpected resources- how might we freely educate
                teenagers about finance?
              </li>
            </ul>
          </p>
          <h3>Sketching</h3>
          <p>
            Teenagers can be considered a category that falls under the Next
            Billion Users ( NBU) so we designed from the bottom up designing
            approach and started with designing the mobile version of the
            product and later adopted it to larger screen sizes. We sketched out
            some concepts for our app’s design based on what we learnt about our
            users in the empathize stage using the crazy eights approach.
          </p>

          <img
            className="image main"
            src={petadoption_paper_wireframes}
            alt=""
          />
          <h3>wireframes</h3>
          <p>
            In this stage we created the first levels of design using what we
            knew about our users and their needs. The headings are clearly
            listed and separated by categories, the lessons are chunked and
            progress of an individual is clearly displayed in a clear and simple
            manner.
          </p>
          <img
            className="image main"
            src={petadoption_paper_wireframes}
            alt=""
          />
          <h3>Lo-fi Prototypes</h3>
          <p>
            The low fidelity prototype connected the primary user flow of
            learning so the prototype could be used in a usability study with
            the users.
          </p>
          <p>View the prototype on link</p>

          <img
            className="image main"
            src={petadoption_digital_wireframes}
            alt=""
          />
          <h3 className="major">Testing</h3>
          <h3>Research and usability Study</h3>
          <p>
            Our goal was to figure out what specific difficulties users
            encounter when they try to complete the core tasks of the financial
            literacy app: updating income, expenses and learning the basics of
            financial control and practically using it in their lives.
          </p>
          <p>
            We conducted an unmoderated usability study remotely between june
            09-16. 5 participants used the app to learn and plan their finances.
            Each session lasted for 30-60 minutes including the test and
            interview questions. Participants were teenagers(Two males, two
            females, and one nonbinary individual, aged 13 to 19 years old) 2
            part time workers 2 dependent on parents and one user with
            disability. They were all living in urban, suburban, and rural
            areas. Assistive technologies (keyboard, screen reader were
            provided)
          </p>
          <p>
            We presented participants with a series of tasks to understand how
            much time users spend on learning and completing a lesson. Later we
            had them complete a System Usability Scale to evaluate feedback and
            noted on a note taking spreadsheet
          </p>
          <img
            className="image main"
            src={petadoption_affinity_diagram}
            alt=""
          />
          <h3>User testing results</h3>
          <p>
            <ul>
              <li>
                Based on the theme that: (Users don't have ability to see what
                course they are taking), an insight is: (add name of course to
                home screen ).
              </li>
              <li>
                Based on the theme that: (Users can’t take multiple courses at
                the same time ), an insight is: (drop down in an area that has
                the title of the course that's been taken so that users can
                select).
              </li>
              <li>
                Based on the theme that: (users want more engaging activity in
                the app), an insight is: (make the app feel like a game and take
                kids from level1 to the next ).
              </li>
              <li>
                Based on the theme that: (Taking notes is annoying for users),
                an insight is: (include area with video transcript).
              </li>
              <li>
                Based on the theme that: (users with vision impairment cannot
                insert answers), an insight is: (include an option to answer
                using microphone).
              </li>
              <li>
                Based on the theme that: (users couldn't close lessons when they
                wanted to), an insight is: (include close option in every
                screen).
              </li>
              <li>
                Based on the theme that: (users who access apps with voice
                assistants have to go through the entire transcript before
                coming to the continue option), an insight is: (include the
                continue button soon after the video in lesson).
              </li>
            </ul>
          </p>
          <h3>Changes</h3>
          <p>
            Early designs allowed for only viewing progress of goal , but after
            the usability studies, we added the learning progress graph to the
            home screen. An ability to quickly get to the lesson and if needed
            to find other suitable lessons was the next addition to the app
            after the usability findings. We also revised the design bringing
            the buttons higher so a user with a voice assistant could navigate
            better on the app.
          </p>
          <img className="image main" src={petadoption_lofi_prototype} alt="" />
          <h3>Mockups and high-fidelity prototype</h3>

          <p>
            At this point in the design cycle, we began to add colours, images,
            and icons to our design. We updated the shape, size and structure of
            content to suit teenagers and improve the user experience.
          </p>

          <p>
            We then moved from designing and prototyping to create a high
            fidelity prototype with animations encouraging teenagers to learn
            and use the service. It was again sent for user testing and improved
            which helped us complete the final prototype which we presented to
            the stakeholders and then passed off to the Development Team for
            production.
          </p>
          <p>View Prototype</p>
          <img className="image main" src={petadoption_after_changes} alt="" />
          <p>View the prototype after the changes from user testing on link</p>
          <h3>Mockups and high-fidelity prototype</h3>
          <p>
            A few A/B tests were conducted to understand if users will prefer
            real life images or illustrations in the home screen and if they
            prefer a slider view or collage view of seeing the images of a
            selected pet. Participants were given both options and asked to talk
            through and explain the screen they selected.{' '}
          </p>
          <img className="image main" src={petadoption_ab_testing} alt="" />
          <p>View the prototype on link</p>
          <h3>Responsive screen design</h3>
          <p>
            Next the screens had to adapt to the device that the user is on. So
            the process to design how the website will appear on smaller screens
            was initiated. The aim of this task was to have a uniform design so
            that users feel familiar across devices and provide a smooth
            uninterrupted experience. First up I drew all the frames on grid
            paper.
          </p>
          <img
            className="image main"
            src={petadoption_scaling_wireframes}
            alt=""
          />
          <p>
            Later digital screens helped test the website on different screen
            sizes before completion of the project.
          </p>
          <img
            className="image main"
            src={petadoption_all_digital_responsive_screens}
            alt=""
          />
          <h3>Accessibility considerations</h3>
          <p>
            The website was designed keeping in mind the Web Content
            Accessibility Guidelines (WCAG).Accessible color and contrast was
            used. Several ways to mark up the design to make it accessible for
            users which was thought of were;
            <ul>
              <li>Navigation order annotations</li>
              <li>Hierarchical headings</li>
              <li>Accessibility labels for screen readers</li>
            </ul>
          </p>
          <img
            className="image main"
            src={petadoption_responsive_mockup}
            alt=""
          />
          <h3 className="major">Reflection</h3>
          <p>
            This project introduced me to Adobe XD. It is very similar to Figma
            yet there are many advantages to designing user experiences in this
            platform. I also understood extremely well on how responsive sites
            and each screen was individually designed and the importance of
            designing to web accessibility standards.
          </p>
          <div className="video-wrapper">
            <iframe
              title="Petadoption website flow video"
              width="560"
              height="315"
              src="https://www.youtube.com/embed/Zp9K9qr7jPk"
              frameborder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
          </div>
          <section className="features">
            <article>
              <a
                href="https://www.gsma.com/mobilefordevelopment/wp-content/uploads/2017/06/govi-mithuru-mobile-agriculture-service-dialog-sri-lanka.pdf"
                target="_blank"
                rel="noopener noreferrer"
                className="image"
              >
                <img src={magri_frog_casestudyimage} alt="" />
              </a>
              <a
                href="https://www.gsma.com/mobilefordevelopment/wp-content/uploads/2017/06/govi-mithuru-mobile-agriculture-service-dialog-sri-lanka.pdf"
                target="_blank"
                rel="noopener noreferrer"
                className="special"
              >
                See the case study
              </a>
            </article>
            <article>
              <a
                href="https://www.dialog.lk/govi-mithuru/"
                target="_blank"
                rel="noopener noreferrer"
                className="image"
              >
                <img src={magri_frog_articleimage} alt="" />
              </a>
              <a
                href="https://www.dialog.lk/govi-mithuru/"
                target="_blank"
                rel="noopener noreferrer"
                className="special"
              >
                Read article
              </a>
            </article>
          </section>
        </div>
      </div>
    </section>
  </Layout>
);

export default IndexPage;
